import React, { useState } from 'react';
import { withRouter } from 'react-router-dom';
import axios from 'axios';
import styled from "styled-components";
import Header from '../../Common/Header';
import Footer from '../../Common/Footer';
import StyledBox from '../../Style/styledBox';
import MyPageButton from '../../Style/MyPageButton';
import MyPageInput from '../../Style/MyPageInput';
import MyPageTitle from '../../Style/MyPageTitle';

const Alert = styled.p`
  color: #757575;
  font-size: 12px;
  font-weight: normal;
  line-height: 18px;
  margin-top: 16px;
`;

function WithDrawal({history}) {
  const [CurrentPassword, setCurrentPassword] = useState("");
  const userFrom = localStorage.getItem('userId');
  const onChangeHandler = (e) => {
    setCurrentPassword(e.currentTarget.value);
  }
  const onSubmitHandler = (e) => {
    e.preventDefault();
    let body = {
      _id: userFrom,
      password: CurrentPassword
    }
    axios.post('/user/withdrawal', body)
      .then((response) => {
        if(!response.data.changeSuccess) {
          alert(response.data.message);
        } else {
          if(response.data.changeSuccess) {
            alert(" .");
            history.push("/");
          } else {
            alert(" .")
          }
        }
      })
    }
    
  return (
    <>
      <Header title='' link="/board" backbutton={true}/>
      <StyledBox>
        <form onSubmit={onSubmitHandler}>
          <MyPageTitle> </MyPageTitle>
          <MyPageInput 
              type="password"
              placeholder=" " 
              value={CurrentPassword} 
              onChange={onChangeHandler} 
          />
          <Alert>
             ,    ,   .
            <br/>     ,   .
            <br/>      .
          </Alert>
          <MyPageButton type="submit"></MyPageButton>
        </form>
      </StyledBox>
      <Footer/>
    </>
  )
}

export default withRouter(WithDrawal);
