import React, { useEffect, useState } from 'react';
import { withRouter } from 'react-router-dom';
import axios from 'axios';
import styled from "styled-components";
import Header from '../../Common/Header';
import Footer from '../../Common/Footer';
import StyledBox from '../../Style/styledBox';
import MyPageTitle from '../../Style/MyPageTitle';
import MyPageInput from '../../Style/MyPageInput';
import MyPageButton from '../../Style/MyPageButton';

const Textbox = styled.div`
  margin-top: 12px;
`;
const Warning = styled.p`
  color: #c62917;
  font-size: 12px;
  line-height: 18px;
  margin-top: 8px;
`;
const Alert = styled.p`
  color: #999;
  font-size: 12px;
  line-height: 18px;
  margin-top: 8px;
`;

function Email({history}) {
  const [inputs, setInput] = useState({
    currentEmail: "",
    currentPassword: "",
  });
  const { currentEmail, currentPassword } = inputs;
  const userFrom = localStorage.getItem('userId');
  
  useEffect(() => {
  axios.get('/user', {_id: userFrom})
    .then((response) => {
      setInput({
        currentEmail: response.data.email,
        currentPassword: "",
      });
    })
  },[userFrom])

  const onChangeHandler = (e) => {
    const { value, name } = e.currentTarget;
    setInput({
      ...inputs,
      [name]: value,
    });
  }

  const onSubmitHandler = (e) => {
    e.preventDefault();
    let body = {
      _id: userFrom,
      password: currentPassword,
      email: currentEmail
    }
    axios.post('/user/update/email', body)
      .then((response) => {
        if(!response.data.changeSuccess) {
          alert(response.data.message);
        } else {
          if(response.data.changeSuccess) {
            history.push("/mypage");
            alert(" .");
          } else {
            alert("  .");
          }
        }
      })
  }

  return (
    <>
      <Header title=" " link="/board" backbutton={true}/>
      <form onSubmit={onSubmitHandler}>
        <StyledBox>
          <MyPageTitle></MyPageTitle>
          <MyPageInput 
            name="currentEmail"
            placeholder="" 
            value={currentEmail} 
            onChange={onChangeHandler}
          />
          <MyPageTitle> </MyPageTitle>
          <MyPageInput 
            type="password"
            name="currentPassword"
            placeholder=" " 
            value={currentPassword} 
            onChange={onChangeHandler}
          />
          <Textbox>
            <Warning>    .</Warning>
            <Alert>    / ,       .</Alert>
          </Textbox>
          <MyPageButton margin="20px 0px 20px 0px"> </MyPageButton>
        </StyledBox>
      </form>
      <Footer/>
    </>
  )
}

export default withRouter(Email)
